.main-nav__toggle {
  padding: 0;
  width: 23px;
  height: 22px;
  border: 0;
  background-color: transparent;
  position: absolute;
  top: 15px;
  right: 20px;
  z-index: 40;
}
.main-nav__toggle--closed {
  z-index: 40;
}
.main-nav__toggle--closed::before {
  position: absolute;
  content: "";
  width: 23px;
  height: 4px;
  border-radius: 5px;
  background-color: #81B3D2;
  box-shadow: 0 10px 0 0 #81B3D2, 0 20px 0 0 #81B3D2;
  z-index: 1000;
  top: 0px;
  right: 0px;
}
.main-nav__toggle--closed:focus::before,
.main-nav__toggle--closed:hover::before {
  background-color: #81B3D260;
  box-shadow: 0 10px 0 0 #81B3D260, 0 20px 0 0 #81B3D260;
}
.main-nav__toggle--opened {
  z-index: 40;
}
.main-nav__toggle--opened::before,
.main-nav__toggle--opened::after {
  content: "";
  position: absolute;
  width: 23px;
  height: 3px;
  border-radius: 5px;
  background-color: #ffffff;
  top: 10px;
  right: 0px;
}
.main-nav__toggle--opened::before {
  transform: rotate(45deg);
}
.main-nav__toggle--opened::after {
  transform: rotate(-45deg);
}
.main-nav__toggle--opened:focus::before,
.main-nav__toggle--opened:focus::after,
.main-nav__toggle--opened:hover::after,
.main-nav__toggle--opened:hover::before {
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .main-nav__toggle {
