.custom-radio {
  position: absolute;
  opacity: 0;
  z-index: -1;
}
.custom-radio + label {
  display: inline-flex;
  align-items: center;
}
.custom-radio + label::before {
  content: "";
  display: inline-block;
  width: 58px;
  height: 58px;
  margin-right: 26px;
  background-color: #f2f2f2;
  flex-shrink: 0;
  flex-grow: 0;
  border-radius: 50%;
}
/* стили для радиокнопки, находящейся в состоянии checked */
.custom-radio:checked + label::before {
  background-color: #333333;
  flex-shrink: 0;
  flex-grow: 0;
  border: 15px solid #e5e5e5;
}
/* стили при наведении курсора на радио */
.custom-radio:not(:disabled):not(:checked) + label:hover::before {
  background-color: #e5e5e5;
}
.custom-radio:not(:disabled):checked + label:hover::before {
  background-color: #333333;
  border-color: #e5e5e5;
}
/* стили при нажатии на неё */
.custom-radio:not(:disabled):active + label::before {
  background-color: #d5d5d5;
}
.custom-checkbox {
  position: absolute;
  z-index: -1;
  opacity: 0;
}
.custom-checkbox + label {
  display: inline-flex;
  align-items: center;
  user-select: none;
}
.custom-checkbox + label::before {
  content: "";
  display: inline-block;
  width: 56px;
  height: 56px;
  flex-shrink: 0;
  flex-grow: 0;
  border-radius: 15%;
  margin-right: 29px;
  background-color: #f2f2f2;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 50% 50%;
}
.custom-checkbox:checked + label::before {
  background-color: #f2f2f2;
  background-image: url("data:image/svg+xml,%3Csvg width='24' height='17' viewBox='0 0 24 17' fill='none' xmlns='http://www.w3.org/2000/svg%27%3E%3Cpath d='M22.925 1.325L21.825 0.225C21.525 -0.075 20.925 -0.075 20.625 0.225L8.725 12.125L2.525 5.925C2.225 5.625 1.625 5.625 1.325 5.925L0.225 7.025C-0.075 7.325 -0.075 7.925 0.225 8.225L8.025 16.025C8.225 16.225 8.425 16.225 8.625 16.225C8.825 16.225 9.025 16.125 9.225 16.025L22.725 2.525C23.225 2.225 23.225 1.725 22.925 1.325Z' fill='black'/%3E%3C/svg%3E%0A");
}
.custom-checkbox:not(:disabled):not(:checked) + label:hover::before {
  background-color: #e5e5e5;
}
