.custom-range {
  -webkit-appearance: none;
  width: 100%;
  overflow: hidden;
  background-color: #00000060;
  height: 45px;
  margin: 0;
  padding: 0 22px;
}
.custom-range:focus {
  outline: none;
}
.custom-range::-webkit-slider-runnable-track {
  width: 100%;
  height: 3px;
  cursor: pointer;
  transition: 0.2s;
  box-shadow: 0px 0px 0px #000000;
  background: #ffffff20;
  border-radius: 0px;
  border: 0px solid #000000;
}
.custom-range::-webkit-slider-thumb {
  box-shadow: 0px 0px 0px #000000;
  border: 0px solid #2497e3;
  height: 13px;
  width: 13px;
  border-radius: 50px;
  background: #ffffff;
  cursor: pointer;
  -webkit-appearance: none;
  margin-top: -5px;
}
.custom-range:focus::-webkit-slider-runnable-track {
  background: #ffffff;
}
.custom-range::-moz-range-track {
  width: 100%;
  height: 3px;
  cursor: pointer;
  transition: 0.2s;
  box-shadow: 0px 0px 0px #000000;
  background: #ffffff20;
  border-radius: 0px;
  border: 0px solid #000000;
}
.custom-range::-moz-range-thumb {
  box-shadow: 0px 0px 0px #000000;
  border: 0px solid #2497e3;
  height: 13px;
  width: 13px;
  border-radius: 50px;
  background: #ffffff;
  cursor: pointer;
}
.custom-range::-ms-track {
  width: 100%;
  height: 3px;
  cursor: pointer;
  transition: 0.2s;
  background: transparent;
  border-color: transparent;
  color: transparent;
}
.custom-range::-ms-fill-lower {
  background: #ffffff;
  border: 0px solid #000000;
  border-radius: 0px;
  box-shadow: 0px 0px 0px #000000;
}
.custom-range::-ms-fill-upper {
  background: #ffffff;
  border: 0px solid #000000;
  border-radius: 0px;
  box-shadow: 0px 0px 0px #000000;
}
.custom-range::-ms-thumb {
  margin-top: 1px;
  box-shadow: 0px 0px 0px #000000;
  border: 0px solid #2497e3;
  height: 13px;
  width: 13px;
  border-radius: 50px;
  background: #ffffff;
  cursor: pointer;
}
.custom-range:focus::-ms-fill-lower {
  background: #ffffff;
}
.custom-range:focus::-ms-fill-upper {
  background: #ffffff;
}
